
.container {
  padding-right: 15px;
  padding-left: 15px;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}

.wrap {
  margin: 0 auto;
  padding-right: 15px;
  padding-left: 15px;
  height: 10%;
  display: flex;
  flex-direction: column;
  //justify-content: center;


  @media screen and (min-width: 960px) {
    flex-direction: row;
  }

}
